import { useContext, useState } from 'react'
import { signup } from '../services/auth.services'
import { useNavigate, useLocation } from 'react-router-dom'
import * as PATHS from '../utils/paths'
import * as USER_HELPERS from '../utils/userToken'
import { AuthContext } from '../context/auth.context'

const Signup = (props) => {
  const location = useLocation()
  let registerAsLandlord
  if (location.state === null) registerAsLandlord = false
  else registerAsLandlord = location.state.landlord
  const { authenticate } = useContext(AuthContext)
  const [form, setForm] = useState({
    email: '',
    password: '',
    name: '',
    lastName: '',
    phone: '',
    isLandlord: false,
  })
  const { email, password, name, lastName, phone, isLandlord } = form

  const [error, setError] = useState(null)
  const navigate = useNavigate()

  const handleInputChange = (event) => {
    const { name, value } = event.target
    let val = null
    if (event.target.type === 'checkbox') {
      val = event.target.checked
    } else {
      val = value
    }
    return setForm({ ...form, [name]: val })
  }

  const handleFormSubmission = async (event) => {
    event.preventDefault()
    const credentials = {
      email,
      password,
      name,
      lastName,
      phone,
      isLandlord,
    }
    const res = await signup(credentials)
    if (!res.status) {
      // unsuccessful signup
      console.error('Signup was unsuccessful: ', res)
      return setError({
        message: 'Signup was unsuccessful! Please check the console.',
      })
    }
    // successful signup
    USER_HELPERS.setUserToken(res.data.accessToken)
    authenticate(res.data.user)
    navigate(PATHS.HOMEPAGE)
  }

  return (
    <div>
      <h1>Sign Up</h1>
      <form onSubmit={handleFormSubmission} className="auth__form">
        <label htmlFor="input-email">Email</label>
        <input
          id="input-email"
          type="email"
          name="email"
          placeholder="Email"
          value={email}
          onChange={handleInputChange}
          required
        />

        <label htmlFor="input-password">Password</label>
        <input
          id="input-password"
          type="password"
          name="password"
          placeholder="Password"
          value={password}
          onChange={handleInputChange}
          required
          minLength="8"
        />

        <label htmlFor="input-name">Name</label>
        <input
          id="input-name"
          type="text"
          name="name"
          placeholder="Name"
          value={name}
          onChange={handleInputChange}
          required
        />

        <label htmlFor="input-lastName">Last name</label>
        <input
          id="input-lastName"
          type="text"
          name="lastName"
          placeholder="Last name"
          value={lastName}
          onChange={handleInputChange}
          required
        />

        <label htmlFor="input-phone">Phone</label>
        <input
          id="input-phone"
          type="tel"
          name="phone"
          placeholder="Phone"
          value={phone}
          onChange={handleInputChange}
          required
        />

        <label htmlFor="input-isLandlord">Are you a Landlord?</label>
        <input
          id="input-isLandlord"
          type="checkbox"
          name="isLandlord"
          value={isLandlord}
          checked={registerAsLandlord}
          onChange={handleInputChange}
        />

        {error && (
          <div className="error-block">
            <p>There was an error submiting the form:</p>
            <p>{error.message}</p>
          </div>
        )}

        <button className="button__submit" type="submit">
          Sign Up
        </button>
      </form>
    </div>
  )
}

export default Signup
