import { Dialog, Transition } from '@headlessui/react'
import { Fragment, useContext, useState } from 'react'
import { AuthContext } from '../../context/auth.context'
import { updateRoom } from '../../services/rooms.services'
import axios from 'axios'
import AutoComplete from '../AutoComplete/AutoComplete'

const UpdateRoomModal = ({
  isOpen,
  closeModal,
  room,
  needRefresh,
  setNeedRefresh,
}) => {
  const [locationInput, setLocationInput] = useState(room?.location)
  const [newImg, setNewImg] = useState(null)
  const [newPreview, setNewPreview] = useState('')
  const { user } = useContext(AuthContext)
  const [error, setError] = useState(null)
  const [updating, setUpdating] = useState(false)

  const [form, setForm] = useState({
    title: room?.title,
    location: room?.location,
    description: room?.description,
    price: room?.price,
    isDouble: room?.isDouble,
    bathrooms: room?.bathrooms,
    totalRooms: room?.totalRooms,
    area: room?.area,
    isAdActive: room?.isAdActive,
    isRented: room?.isRented,
  })

  const previewImg = (file) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onloadend = () => {
      setNewPreview(reader.result)
    }
  }

  const handleInputChange = (event) => {
    const { name, value } = event.target
    let val = null
    if (event.target.type === 'checkbox') {
      val = event.target.checked
    } else {
      val = value
    }
    return setForm({ ...form, [name]: val })
  }

  const handleFormSubmission = async (event) => {
    event.preventDefault()
    setUpdating(true)

    try {
      let cloudinaryRes
      if (newImg) {
        const formData = new FormData()
        const id = user._id + '_' + Date.now()
        formData.append('file', newImg)
        formData.append('public_id', id)
        formData.append('upload_preset', 'hhUpload')
        cloudinaryRes = await axios.post(
          'https://api.cloudinary.com/v1_1/fede4954/image/upload',
          formData
        )
      }

      const roomInfo = {
        ...form,
        location: locationInput,
        roomId: room?._id,
        oldImg: room?.image,
        ...(cloudinaryRes && { newImg: cloudinaryRes.data.public_id }),
      }

      // Succesful room update
      await updateRoom(roomInfo)
      setUpdating(false)

      setNeedRefresh(!needRefresh)
    } catch (err) {
      // Unsuccesful room update
      console.error('Room update was unsuccessful: ', err)
      return setError({
        message: 'Room update was unsuccessful! Please check the console.',
      })
    }
  }

  if (!room) return null

  return (
    <>
      <Transition appear show={isOpen} as={Fragment}>
        <Dialog
          as="div"
          className="fixed inset-0 z-10 overflow-y-auto"
          onClose={closeModal}
        >
          <div className="min-h-screen px-4 text-center">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0" />
            </Transition.Child>

            {/* This element is to trick the browser into centering the modal contents. */}
            <span
              className="inline-block h-screen align-middle"
              aria-hidden="true"
            >
              &#8203;
            </span>
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <div className="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded-2xl">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-gray-900"
                >
                  Update room
                </Dialog.Title>
                <form
                  onSubmit={handleFormSubmission}
                  className="room__form flex flex-col w-2/4 mx-auto"
                >
                  <label htmlFor="image">image</label>
                  <input
                    id="image"
                    type="file"
                    name="image"
                    onChange={(event) => {
                      previewImg(event.target.files[0])
                      setNewImg(event.target.files[0])
                    }}
                    required
                  />

                  <label htmlFor="title">Title</label>
                  <input
                    id="title"
                    type="text"
                    name="title"
                    placeholder="Room title"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    defaultValue={room?.title}
                    required
                  />

                  <h3>Current location: {room?.location}</h3>
                  <AutoComplete
                    location={locationInput}
                    setLocationInput={setLocationInput}
                  />

                  <label htmlFor="description">Description</label>
                  <textarea
                    id="description"
                    name="description"
                    placeholder="Room description"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    defaultValue={room?.description}
                  ></textarea>

                  <label htmlFor="price">Price</label>
                  <input
                    id="price"
                    type="number"
                    name="price"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    defaultValue={room?.price}
                    required
                  />

                  <label htmlFor="isDouble">Double bedroom?</label>
                  <input
                    id="isDouble"
                    type="checkbox"
                    name="isDouble"
                    onChange={handleInputChange}
                    defaultChecked={room?.isDouble}
                  />

                  <label htmlFor="bathrooms">Number of bathrooms</label>
                  <input
                    id="bathrooms"
                    type="number"
                    name="bathrooms"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    required
                    defaultValue={room?.bathrooms}
                  />

                  <label htmlFor="totalRooms">
                    Number of rooms of the house
                  </label>
                  <input
                    id="totalRooms"
                    type="number"
                    name="totalRooms"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    required
                    defaultValue={room?.totalRooms}
                  />

                  <label htmlFor="area">Total area of the house in m2</label>
                  <input
                    id="area"
                    type="number"
                    name="area"
                    onChange={handleInputChange}
                    className="bg-gray-300"
                    required
                    defaultValue={room?.area}
                  />
                  <label htmlFor="isAdActive">Active ad</label>
                  <input
                    id="isAdActive"
                    type="checkbox"
                    name="isAdActive"
                    onChange={handleInputChange}
                    defaultChecked={room?.isAdActive}
                  />
                  <label htmlFor="isRented">Currently rented</label>
                  <input
                    id="isRented"
                    type="checkbox"
                    name="isRented"
                    onChange={handleInputChange}
                    defaultChecked={room?.isRented}
                  />

                  {error && (
                    <div className="error-block">
                      <p>There was an error submiting the form:</p>
                      <p>{error.message}</p>
                    </div>
                  )}

                  <div className="mt-4">
                    {updating ? (
                      <button className="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500">
                        Updating room...
                      </button>
                    ) : (
                      <button
                        type="button"
                        className="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                        onClick={(event) => {
                          handleFormSubmission(event)
                          closeModal()
                        }}
                      >
                        Update this room
                      </button>
                    )}
                  </div>

                  {newPreview && <img src={newPreview} alt="preview" />}
                </form>
              </div>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition>
    </>
  )
}

export default UpdateRoomModal
