import { useEffect } from 'react'
import { useParams } from 'react-router'
import { useState } from 'react/cjs/react.development'
import { getRoom } from '../services/rooms.services'
import { Image } from 'cloudinary-react'
import LoadingComponent from '../components/Loading'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import { faShareAlt } from '@fortawesome/free-solid-svg-icons'

const RoomInfo = () => {
  const params = useParams()
  const roomId = params.id
  const [room, setRoom] = useState()
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadRoom = async () => {
      const res = await getRoom(roomId)
      setRoom(res.data.room)
      setLoading(false)
    }

    loadRoom()
  }, [roomId])

  return (
    <div>
      {loading ? (
        <LoadingComponent />
      ) : (
        <>
          <h1 className="bg-red-500">{room?.publishedBy.email}</h1>
          <div className=""></div>
          <Image cloudName="fede4954" publicId={room?.image} className="" />
          <h1>room: {room?.title}</h1>

          <p>
            <a
              href={`https://wa.me/?text=${window.location.href}`}
              target="_blank"
              rel="noreferrer"
            >
              <FontAwesomeIcon icon={faShareAlt} />
              Share on WhatsApp
            </a>
          </p>
          <p>
            <a
              href={`https://wa.me/34${room?.publishedBy.phone}?text=I'm%20interested%20in%20the%20ad%20you%20published%20in%20Home%20Hopper`}
              target="_blank"
              rel="noreferrer"
            >
              <FontAwesomeIcon icon={faWhatsapp} />
              Contact the owner
            </a>
          </p>
        </>
      )}
    </div>
  )
}

export default RoomInfo
