import * as PATHS from '../utils/paths'
import { Link } from 'react-router-dom'
import { useContext, useEffect, useState } from 'react'
import DeleteRoomButton from '../components/DeleteRoomButton/DeleteRoomButton'
import UpdateRoomModal from '../components/UpdateRoomModal/UpdateRoomModal'
import { AuthContext } from '../context/auth.context'
import { getUserRooms } from '../services/rooms.services'
import LoadingComponent from '../components/Loading'

const YourRooms = () => {
  const [rooms, setRooms] = useState([])
  const [needRefresh, setNeedRefresh] = useState(false)
  let [isOpen, setIsOpen] = useState(false)
  let [modalRoom, setModalRoom] = useState(null)
  const [loading, setLoading] = useState(true)
  const { user } = useContext(AuthContext)
  const closeModal = () => {
    setIsOpen(false)
  }

  const openModal = () => {
    setIsOpen(true)
  }

  useEffect(() => {
    const getRooms = async () => {
      const res = await getUserRooms({ id: user._id })
      setRooms(res.data.userRooms)
      setLoading(false)
    }
    getRooms()
  }, [needRefresh, user])

  return (
    <>
      {loading ? (
        <LoadingComponent />
      ) : (
        <>
          <Link to={PATHS.NEWROOM} className="bg-green-400 p-2">
            New room
          </Link>
          <ul>
            {rooms.map((room, index) => {
              return (
                <li key={index + Date.now()}>
                  <div className="w-2/4 bg-gray-400 my-8">
                    <h3>Title: {room.title}</h3>
                    <DeleteRoomButton
                      id={room._id}
                      setRooms={setRooms}
                      rooms={rooms}
                    />
                    <button
                      onClick={() => {
                        openModal()
                        setModalRoom(room)
                      }}
                      className="bg-blue-500"
                    >
                      Edit room
                    </button>
                  </div>
                </li>
              )
            })}
          </ul>
          <UpdateRoomModal
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            openModal={openModal}
            closeModal={closeModal}
            room={modalRoom}
            needRefresh={needRefresh}
            setNeedRefresh={setNeedRefresh}
          />
        </>
      )}
    </>
  )
}
export default YourRooms
