import { useState, useContext, useEffect } from 'react'
import { updateProfile } from '../services/profile.services'
import { AuthContext } from '../context/auth.context'
import { getSavedRooms } from '../services/profile.services'
import LoadingComponent from '../components/Loading'

const Profile = () => {
  const { user, setUser } = useContext(AuthContext)

  const [form, setForm] = useState({
    email: user.email,
    name: user.name,
    lastName: user.lastName,
    phone: user.phone,
  })

  const [error, setError] = useState(null)
  const [rooms, setRooms] = useState([])
  const [loading, setLoading] = useState(true)
  const [updating, setUpdating] = useState(false)
  useEffect(() => {
    const loadSavedRooms = async () => {
      const res = await getSavedRooms(user._id)
      setRooms(res.data)
      setLoading(false)
    }
    loadSavedRooms()
  }, [user._id])

  const handleInputChange = async (event) => {
    const { name, value } = event.target
    return setForm({ ...form, [name]: value })
  }

  const handleFormSubmission = async (event) => {
    setUpdating(true)
    event.preventDefault()
    const credentials = { ...form, userId: user._id }
    const res = await updateProfile(credentials)
    setUser(res.data)
    setUpdating(false)
    if (!res.status) {
      console.error('Signup was unsuccessful: ', res)
      return setError({
        message: 'Signup was unsuccessful! Please check the console.',
      })
    }
  }

  return (
    <>
      {loading ? (
        <LoadingComponent />
      ) : (
        <div>
          <h1>{user._id}</h1>
          <h1>Hello, {user.name}</h1>
          <form onSubmit={handleFormSubmission} className="flex flex-col">
            <label htmlFor="name">Name: </label>
            <input
              id="name"
              type="text"
              name="name"
              placeholder="Your name"
              className="bg-gray-300"
              defaultValue={user.name}
              onChange={handleInputChange}
              required
            />

            <label htmlFor="lastName">Last name: </label>
            <input
              id="lastName"
              type="text"
              name="lastName"
              placeholder="Your last name"
              className="bg-gray-300"
              defaultValue={user.lastName}
              onChange={handleInputChange}
              required
            />

            <label htmlFor="email">Email: </label>
            <input
              id="email"
              type="email"
              name="email"
              placeholder="Your email "
              className="bg-gray-300"
              defaultValue={user.email}
              onChange={handleInputChange}
              required
            />

            <label htmlFor="phone">Phone: </label>
            <input
              id="phone"
              type="tel"
              name="phone"
              placeholder="Your phone number"
              className="bg-gray-300"
              defaultValue={user.phone}
              onChange={handleInputChange}
              required
            />

            {error && (
              <div className="error-block">
                <p>There was an error updating your profile:</p>
                <p>{error.message}</p>
              </div>
            )}
            {updating ? (
              <button>Updating your profile...</button>
            ) : (
              <button type="submit">Update your profile</button>
            )}
          </form>
          <div className="bg-blue-500">
            {rooms?.map((room, index) => {
              return (
                <p key={index + Date.now()}>
                  {room.title} and {room.price}
                </p>
              )
            })}
          </div>
        </div>
      )}
    </>
  )
}

export default Profile
