import axios from 'axios'
import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { createRoom } from '../services/rooms.services'
import * as PATHS from '../utils/paths'
import AutoComplete from '../components/AutoComplete/AutoComplete'
import { AuthContext } from '../context/auth.context'

const NewRoom = () => {
  const { user } = useContext(AuthContext)
  const [img, setImg] = useState('')
  const [preview, setPreview] = useState('')
  const [locationInput, setLocationInput] = useState('')
  const [loading, setLoading] = useState(false)

  const [form, setForm] = useState({
    title: '',
    description: '',
    price: 0,
    isDouble: false,
    bathrooms: 0,
    totalRooms: 0,
    area: 0,
    isAdActive: false,
    isRented: false,
  })
  const {
    title,
    description,
    price,
    isDouble,
    bathrooms,
    totalRooms,
    area,
    isAdActive,
    isRented,
  } = form

  const [error, setError] = useState(null)
  const navigate = useNavigate()

  const previewImg = (file) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onloadend = () => {
      setPreview(reader.result)
    }
  }

  const handleInputChange = async (event) => {
    const { name, value } = event.target
    let val = null
    if (event.target.type === 'checkbox') {
      val = event.target.checked
    } else {
      val = value
    }
    return setForm({ ...form, [name]: val })
  }

  const handleFormSubmission = async (event) => {
    event.preventDefault()
    setLoading(true)
    try {
      const formData = new FormData()
      const id = user._id + '_' + Date.now()
      formData.append('file', img)
      formData.append('public_id', id)
      formData.append('upload_preset', 'hhUpload')
      const cloudinaryRes = await axios.post(
        'https://api.cloudinary.com/v1_1/fede4954/image/upload',
        formData
      )

      let roomInfo = {
        ...form,
        image: cloudinaryRes.data.public_id,
        location: locationInput,
        publishedBy: user?._id,
      }

      // successful ROOM CREATION
      await createRoom(roomInfo)
    } catch (err) {
      // unsuccessful room creation
      console.error('Room creating was unsuccessful: ', err)
      return setError({
        message: 'Room creation was unsuccessful! Please check the console.',
      })
    }

    navigate(PATHS.YOURROOMS)
  }

  return (
    <div>
      <h1>Create new room</h1>
      <form
        onSubmit={handleFormSubmission}
        className="room__form flex flex-col w-2/4 mx-auto"
      >
        <label htmlFor="image">image</label>
        <input
          id="image"
          type="file"
          name="image"
          onChange={(event) => {
            previewImg(event.target.files[0])
            setImg(event.target.files[0])
          }}
          required
        />

        <label htmlFor="title">Title</label>
        <input
          id="title"
          type="text"
          name="title"
          placeholder="Room title"
          value={title}
          onChange={handleInputChange}
          className="bg-gray-300"
          required
        />

        <AutoComplete
          locationInput={locationInput}
          setLocationInput={setLocationInput}
        />

        <label htmlFor="description">Description</label>
        <textarea
          id="description"
          name="description"
          placeholder="Room description"
          value={description}
          onChange={handleInputChange}
          className="bg-gray-300"
        ></textarea>

        <label htmlFor="price">Price</label>
        <input
          id="price"
          type="number"
          name="price"
          value={price}
          onChange={handleInputChange}
          className="bg-gray-300"
          required
        />

        <label htmlFor="isDouble">Double bedroom?</label>
        <input
          id="isDouble"
          type="checkbox"
          name="isDouble"
          value={isDouble}
          onChange={handleInputChange}
        />

        <label htmlFor="bathrooms">Number of bathrooms</label>
        <input
          id="bathrooms"
          type="number"
          name="bathrooms"
          value={bathrooms}
          onChange={handleInputChange}
          className="bg-gray-300"
          required
        />

        <label htmlFor="totalRooms">Number of rooms of the house</label>
        <input
          id="totalRooms"
          type="number"
          name="totalRooms"
          value={totalRooms}
          onChange={handleInputChange}
          className="bg-gray-300"
          required
        />

        <label htmlFor="area">Total area of the house in m2</label>
        <input
          id="area"
          type="number"
          name="area"
          value={area}
          onChange={handleInputChange}
          className="bg-gray-300"
          required
        />
        <label htmlFor="isAdActive">Active ad</label>
        <input
          id="isAdActive"
          type="checkbox"
          name="isAdActive"
          value={isAdActive}
          onChange={handleInputChange}
        />
        <label htmlFor="isRented">Currently rented</label>
        <input
          id="isRented"
          type="checkbox"
          name="isRented"
          value={isRented}
          onChange={handleInputChange}
        />

        {error && (
          <div className="error-block">
            <p>There was an error submiting the form:</p>
            <p>{error.message}</p>
          </div>
        )}
        {loading ? (
          <button className="button__submit bg-red-500">
            Creating room...
          </button>
        ) : (
          <button className="button__submit bg-red-500" type="submit">
            Create room
          </button>
        )}
      </form>

      {preview && <img src={preview} alt="preview" />}
    </div>
  )
}

export default NewRoom
